@use "sass:math";
@import '../common-imports';

.chart-with-animation{
    // position: sticky;
    // top: 0;
    figure{
        padding-bottom: 47.917%;
    }

    &--850-1200{
        max-width: 1200px;
        margin: 0 auto;
        padding-bottom: calc(850 / 1200) * 100%;
        position: relative;

        video{
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            object-fit: cover;
            width: 100%;
            height: 100%;
        }
    }
    &--800-1200{
        max-width: 1200px;
        margin: 0 auto;
        padding-bottom: calc(800 / 1200) * 100%;
        position: relative;

        video{
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            object-fit: cover;
            width: 100%;
            height: 100%;
        }
    }
}


